export default{
	data() {
		return {
			verification: '获取验证码',
			disabled:false,
			accounts:'',
			verify:'',
			pwd:'',
			as_pwd:''
			
		}
	},
	methods:{
		async accomplish(){ //提交
			var that = this
			if(that.pwd != that.as_pwd){
				uni.showToast({
					icon:'none',
					title:'密码不一致'
				})
				return
			}
			let [error, res] = await uni.request({  //验证验证码
				url:that.APIconfig.api_root.login.findPassword,
				method:'POST',
				data:{
					mobile:that.accounts,
					time:Date.parse(new Date())/1000, //时间戳
					sms_code:that.verify
				}
			})
			if(res.data.code == 0){
				let [reset_err,reset_data] = await reset()
				console.log(reset_data)
				uni.showToast({
					icon:'none',
					title:reset_data.data.msg
				})
				if(reset_data.data.code == 0){
					
				}
			}else{
				uni.showToast({
					icon:'none',
					title:res.data.msg
				})
			}
			function reset(){ //重置密码
				let P = uni.request({ 
					url:that.APIconfig.api_root.login.reset,
					method:'POST',
					data:{
						mobile:that.accounts,
						time:Date.parse(new Date())/1000, //时间戳
						password:that.pwd,
						repeat_password:that.as_pwd,
					}
				})
				return P
			}
			
		},
		obtain(){ //获取验证码
			let that = this
			if(!(/^1[3-9][0-9]\d{8,11}$/.test(that.accounts))){
				uni.showToast({
					icon: 'none',
					title: '手机号码格式不正确'
				});
				return;
			}
			if(that.disabled == true) return
			let data = {
				mobile:that.accounts,
				time:Date.parse(new Date())/1000  //时间戳
			}
			console.log(data)
			uni.request({
				url:that.APIconfig.api_root.login.sendPhone,
				method:'POST',
				data,
				success(res) {
					console.log(res)
					let data = res.data 
					console.log(data)
					uni.showToast({
						icon:'none',
						title:data.msg
					})
					if(data.code == 0){
						that.verification = '60s'
						that.disabled = true
						that.timer = setInterval(function(){
							let num = that.verification.split('s')[0]
							num --
							that.verification = num+'s'
						},1000)
					}
					
				}
			})
			
		},
		
	},
	watch:{
		verification(curval,oldval){// 监听定时器的num值
			if(curval == '-1s'){
				clearInterval(this.timer)
				this.verification = '重新获取'
				this.disabled = false
			}
		}
	},
}
